import axios from 'axios'

export default {
  state: {
    radio: new Audio('http://air2.radiorecord.ru:9003/rr_320'),
    player: {
      volume: 60,
      paused: true,
    },
    stations: [],
    images: [],
    currentData: [],
    selectedStationIndex: 0,
    refreshRadioData: null // setInterval
  },
  mutations: {
    SET_RADIO_STATIONS(state, data) {
      state.stations = data;
    },
    SET_RADIO_IMAGES(state, data) {
      state.images = data
    },
    SELECT_STATION(state, index) {

      state.selectedStationIndex === index
        ? state.player.paused = !state.player.paused
        : state.player.paused = false

      state.selectedStationIndex = index
 
      state.radio.src = state.stations[index].stream_320

    },
    RADIO_PLAY_CONTROL(state) {
      state.player.paused ? state.radio.pause() : state.radio.play()
    }
  },
  actions: {
    async GET_RADIO_STATIONS({commit}) {
      let {data} = await axios.get('https://www.radiorecord.ru/radioapi/stations/', {

      })

      commit('SET_RADIO_STATIONS', data.result)
    },
    async GET_RADIO_IMAGES({commit}) {
      let {data} = await axios.get('https://www.radiorecord.ru/api/stations/now/', {

      });
      
      // commit('SET_RADIO_IMAGES', data.result)
    },
    GET_ALL_DATA({dispatch}) {
      dispatch('GET_RADIO_STATIONS')
      // dispatch('GET_RADIO_IMAGES')
      dispatch('REFRESH_DATA')
    },
    REFRESH_DATA({dispatch}) {
      dispatch('DESTROY_INTERVAL')
      window.getRadioData = setInterval(() => dispatch('GET_ALL_DATA'), 5000)
    },
    DESTROY_INTERVAL(state) {
      clearInterval(window.getRadioData)
      window.getRadioData = null
    },
    RADIO_CONTROL({state, commit}, payload) {
      state.player.paused = payload
      commit('RADIO_PLAY_CONTROL')
    }
  }
}