import Vue from 'vue'

export default {
  state: [
    {number: 463465, list: [
      {from: 45848585, text: 'Привет', time: 1608644663615},
      {from: 454545, text: 'Привет', read: false, time: 1608644672595},
    ]}
  ],
  mutations: {
    PUSH_MESSAGE(state, data) {

      data.to = parseInt(data.to)

      let findMsg = state.find(({number}) => number === data.to);

      findMsg 
        ?  findMsg.list.push(data.message)
        :  state.push(
          {number: data.to, list: [data.message]}
        )
    }
  },
  actions: {
    READ_MESSAGE({state, rootState}, phone) {
      let findMsg = state.find(({number}) => number == phone);

      if (findMsg) {
        findMsg.list.map((msg) => 
          msg.read === false && msg.from != rootState.player.phoneNumber 
          ? Vue.delete(msg, 'read') : null)
      }
    },
    SEND_MESSAGE({commit, rootState}, data) {
      commit('PUSH_MESSAGE', {
        message: {
          from: rootState.player.phoneNumber,
          text: data.msg,
          time: new Date().getTime(),
          read: false
        },
        to: data.to
      })
    }
  },
  getters: {
    GET_UNREAD_MESSAGES_COUNT(state, getters, rootState) {
      let acc = 0;

      state.map(({list}) => 
        list.map((msg) => 
          msg.read === false && msg.from != rootState.player.phoneNumber ? acc++ : null)
      );

      acc > 99 ? acc = 99 : acc
      return acc
    }
  }
}