import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home'
import Calls from '../views/Calls'
import Messages from '../views/Messages'

Vue.use(VueRouter)

const routes = [
  // Главная страница
  {
    path: '/',
    name: 'HomePage',
    component: Home
  },
  // Приложение "Звонки"
  {
    path: '/calls',
    name: 'CallsPage',
    component: Calls,
    children: [
      {
        name: 'RecentCalls',
        path: 'recent',
        component: () => import('@/components/Calls/Recent')
      },
      {
        name: 'ContactsList',
        path: 'contacts',
        component: () => import('@/components/Calls/Contacts')
      },
      {
        name: 'FavouriteContacts',
        path: 'favourite',
        component: () => import('@/components/Calls/Favourite')
      },
      {
        name: 'EnterPhone',
        path: 'enter',
        component: () => import('@/components/Calls/EnterPhone')
      }
    ]
  },
  // Создать контакт. Не дочерний роут. Принадлежит к звонкам
  {
    name: 'CreateContact',
    path: '/create/:options',
    props: true,
    component: () => import('@/components/Calls/AddContact')
  },
  // Информация о номере. Не дочерний роут. Принадлежит к звонкам
  {
    name: 'ContactInfo',
    path: '/info/:phone',
    props: true,
    component: () => import('../components/Calls/ContactInfo')
  }, 
  // Написать сообщение
  {
    name: 'WriteMessage',
    path: '/write',
    component: () => import('@/components/Messages/WriteMessage')
  },
  // Приложение "Сообщения"
  {
    name: 'Messages',
    path: '/messages',
    component: Messages,
  },
  // Переписка с пользователем
  {
    name: 'MessageUser',
    path: '/message/:number',
    props: true,
    component: () => import('@/components/Messages/MessageUser')
  },
  // Приложение "Навгиатор (GPS)"
  {
    name: 'GPS',
    path: '/gps',
    component: () => import('@/views/GPS')
  },
  // Приложение "Музыка"
  {
    name: 'Music',
    path: '/music',
    component: () => import('@/views/Music')
  },
  // Приложение "Настройки"
  {
    name: 'Settings',
    path: '/settings',
    component: () => import('@/views/Settings')
  },
  {
    name: 'SettingsList',
    path: '/settings-list',
    props: true,
    component: () => import('@/components/Settings/SettingsList'),
    children: [
      {
        name: 'SettingWallpapers',
        path: 'wallpapers',
        props: true,
        component: () => import('@/components/Settings/Wallpapers')
      },
    ]
  },
  // Приложение "заметки"
  {
    name: 'NotesList',
    path: '/notes',
    component: () => import('@/views/Notes')
  },
  {
    name: 'Note',
    path: '/note/:time',
    props: true,
    component: () => import('@/components/Notes/Note')
  },
  // Приложение банка 
  {
    name: 'Bank', 
    path: '/bank',
    component: () => import('@/views/Bank')
  },
  {
    name: 'Payment',
    path: '/payment',
    component: () => import('@/components/Bank/Payment'),
    children: [
      {
        name: 'Payment-Buisness',
        path: 'buisness',
        component: () => import('@/components/Bank/Payments/Buisness')
      },
      {
        name: 'Payment-Home',
        path: 'house',
        component: () => import('@/components/Bank/Payments/House')
      },
      {
        name: 'Payment-Phone',
        path: 'phone',
        component: () => import('@/components/Bank/Payments/Phone')
      },
      {
        name: 'Payment-Send',
        path: 'pay',
        component: () => import('@/components/Bank/Payments/PaymentSend')
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
