<template lang="pug">
  
  .outward-call

    .call-actions.call-actions_incoming
      .call-actions__item(@click='rejectCall')
        .call-actions__icon.call-actions__icon_reject
          svg
            use(xlink:href='#call') 
        p.call-actions__text Отклонить

      .call-actions__item(@click='acceptCall')
        .call-actions__icon.call-actions__icon_accept
          svg
            use(xlink:href='#call') 
        p.call-actions__text Принять


</template>

<script>

  import {mapState, mapMutations} from 'vuex'

  export default {
    data() {
      return { isPhoneHidden: false }
    },
    computed: {
      ...mapState(['call']),
    },
    methods: {
      ...mapMutations({
        phoneHidden: 'SET_PHONE_REFLECTION',
        ringtoneControl: 'RINGTONE_CONTROL',
        callStatus: 'SET_CALL_STATUS',
        addToRecent: 'ADD_CALL_TO_RECENT',
        pick: 'SET_PICK_STATUS',
      }),
      pushToRecent() {
        this.addToRecent({number: this.call.interlocutor, type: 'missed', time: new Date().getTime()})
      },
      acceptCall() {
        this.pick(true)
      },
      rejectCall() {
        this.phoneHidden(this.isPhoneHidden)
        this.callStatus(false)
        this.pushToRecent()
        this.$alert.show('Вызов отклонен')
      }
    },
    created() {
      
      clearTimeout(window.callTimeout)

      this.isPhoneHidden = this.$store.state.phoneHidden

      this.phoneHidden(false)
      this.ringtoneControl(true)

      window.callTimeout = setTimeout(() => {
        this.callStatus(false)
        this.phoneHidden(this.isPhoneHidden)

        this.pushToRecent()

        this.$alert.show('Пропущенный звонок')

      }, 20000)
    },
    beforeDestroy() {
      this.ringtoneControl(false)
    }
  }
</script>