<template lang="pug">
  
  .outward-call
    .outward-call__options
      .outward-call__item(@click='mute')
        .outward-call__icon(:class='{"outward-call__icon_active": call.muted}')
          svg
            use(xlink:href='#micro')
        p.outward-call__title Заглушить

      - for (let x = 0; x < 5; x++) 
        .outward-call__item
          .outward-call__icon
          

    .call-actions.call-actions_outward
      .call-actions__item(@click='rejectCall')
        .call-actions__icon.call-actions__icon_reject
          svg
            use(xlink:href='#call') 


</template>


<script>

  import {mapState,mapMutations} from 'vuex'

  export default {
    props: {
      callTime: String
    },
    computed: {
      ...mapState(['call']),
    },
    methods: {
      ...mapMutations({
        callStatus: 'SET_CALL_STATUS',
        addToRecent: 'ADD_CALL_TO_RECENT',
        muteCall: 'MUTE_CALL'
      }),
      pushToRecent() {
        this.addToRecent({number: this.call.interlocutor, type: this.call.type, time: new Date().getTime()})
      },
      rejectCall() {
        this.callStatus(false)
        this.call.picked ? (
          this.pushToRecent(),
          this.$alert.show('Звонок завершен. \n Длительность: ' + this.callTime)
          ) : null
        
      },
      mute() {
        this.muteCall(!this.call.muted)
      }
    },
    created() {
      clearTimeout(window.callTimeout)
    }
  }

</script>
