<template lang="pug">
  
  .messages-wrapper
    .header
      .nav
        router-link(:to="{name: 'WriteMessage'}")
          svg.nav__option.nav__option_icon
            use(xlink:href='#typeMessage')

      h1.header__title Сообщения

      .header-search
        svg.header-search__icon
          use(xlink:href='#Search')
        input(
          type='text' 
          placeholder='Поиск'
          v-model.trim='search'
          @input='searchMessages').header-search__input

    MessageItem(:messages='filteredMessages')
        

</template>

<script>

  import {mapState} from 'vuex'
  import MessageItem from '../components/Messages/MessageItem'

  export default {
    components: {
      MessageItem
    },
    data() {
      return {
        search: '',
        filteredMessages: []
      }
    },
    computed: {
      ...mapState({
        messages: state => state.messages
      })
    },
    methods: {
      searchMessages() {

        if (!this.search)
          return this.filteredMessages = this.messages
      
        this.filteredMessages = []

        let copyMessages = JSON.parse(JSON.stringify(this.messages));

        copyMessages.map(item => {
          item.list.some((msg, index) => {
            if (msg.text.toLowerCase().includes(this.search.toLowerCase())) {

              Object.assign(item.list.last(), {text: msg.text, time: msg.time})

              index === (item.list.length - 1)
                ? null
                : item.list.splice(index, 1)
              
              return this.filteredMessages.push(item)

            }
          })
        })
      }
    },
    created() {
      this.filteredMessages = this.messages
      this.$store.commit('REFRESH_SYSTEM_STYLE', true);
    }
  }
</script>

<style lang="sass">

  @import '../assets/sass/phone/apps/messages/messages'

</style>