<template lang="pug">

  .screen__off

</template>

<script>

  import gsap from 'gsap'

  import {mapState, mapMutations} from 'vuex'

  export default {
    data() {
      return {tl: gsap.timeline(), showInterval: 0, hideInterval: 0}
    },
    computed: mapState(['phoneHidden']),
    methods: {
      ...mapMutations({
        setReflection: 'SET_PHONE_REFLECTION'
      }),
      showPhone() {

        this.showInterval = new Date().getTime()

        this.tl
          .to('.phone', 0.35, {y: '0%', ease: 'power2.out'})
          .to('.screen__off', 0.25, {opacity: 0, display: 'none'}, '-=0.11')
      },
      hidePhone() {

        this.hideInterval = new Date().getTime()

        this.tl
          .to('.screen__off', 0.2, {opacity: 1, display: 'block', ease: 'power2.out'})
          .to('.phone', 0.35, {y: '120%'}, '-=0.05')
      }
    },
    watch: {
      phoneHidden(bool) {
        this.tl = gsap.timeline()
        bool ? this.hidePhone() : this.showPhone()
      }
    },
    created() {
      window.onkeyup = (e) => {

        let time = new Date().getTime()

        if (e.which == 38) 
          time > this.showInterval + 1500 ? this.setReflection(false) : null
          
        else if (e.which == 40)
          time > this.hideInterval + 1500 ? this.setReflection(true) : null
      }
    }
  }

</script>
