<template lang="pug">
  
  .phone-control
    h1.phone-control__title Инструменты для теста телефона
    
    .phone-control__item
      p.phone-control__text.
        Прислать #[b сообщение] 
        от номера #[input(type='number' v-model.trim='message.to')] , с текстом #[input(type='text' v-model='message.text')]
        #[button(@click='sendMessage') Отправить]

    .phone-control__item
      p.phone-control__text.
        Принять звонок от номера: #[input(type='number' v-model='call.number')]
        #[button(@click='makeCall') Позвонить]

    .phone-control__item
      p.phone-control__text.
        Установить баланс банковского счета #[input(type='number' v-model.trim='bank.balance')]
        #[button(@click='setBalance') Установить] 

    .phone-control__item
      p.phone-control__text.
        Пополнить баланс банковского счета (будет прислано уведомление и записано в историю платежей) #[br]
        Отправить денег:#[input(type='number' v-model.trim='bank.money')]
        #[button(@click='sendMoney') Отправить] 


</template>

<script>

  import {required} from 'vuelidate/lib/validators'

  export default {
    data() {
      return {
        message: {
          to: '',
          text: ''
        },
        call: {
          number: ''
        },
        bank: {
          balance: '',
          money: ''
        }
      }
    },
    validations: {
      message: {
        to: {required},
        text: {required}
      },
      call: {
        number: {required}
      },
      bank: {
        balance: {required},
        money: {required}
      }
    },
    methods: {
      sendMessage() {

        this.$v.$touch()
        if (this.$v.message.$error) return alert('Введи данные')
        
        this.$store.commit('PUSH_MESSAGE', {
          message: {
            from: this.message.to,
            text: this.message.text,
            time: new Date().getTime(),
            read: false
          },
          to: this.message.to
      });

      },
      makeCall() {
        this.$v.$touch()

        if (this.$v.call.$error) return alert('Введи номер с которого нужно звонить')

        this.$store.dispatch('MAKE_CALL', {interlocutor: this.call.number, type: 'incoming'})
      },
      setBalance() {
        this.$v.$touch()

        if (this.$v.bank.balance.$error) return alert('Введи баланс')

        this.$store.state.bank.balance = parseFloat(this.bank.balance)
      },
      sendMoney() {
        // MAKE_DEPOSIT

        this.$v.$touch()

        if (this.$v.bank.money.$error) return alert('Введи сумму')

        this.$store.commit('MAKE_DEPOSIT', {
          title: 'Пополнение счета', type: 'deposit', money: parseFloat(this.bank.money), 
        })
      }
    },
  }

</script>

<style lang="sass">

  .phone-control
    position: absolute
    left: 20px
    top: 20px
    font-family: $sf_display
    &__title 
      font-size: 25px
    &__item
      margin: 10px 0
      line-height: 2
    input
      width: 120px
    button
      margin: 0 0 0 10px
      padding: 2px 5px

</style>