<template lang="pug">
  
  .active-call.full-screen
    .active-call__interlocutor
      h1.active-call__name {{getInterlocutorName}}
      p.active-call__from {{showCallInfo}}

    component(:is='showCallType' :callTime='callTime')

</template>

<script>

  import {mapState} from 'vuex'
  import IncomingCall from '@/components/ActiveCall/IncomingCall'
  import OutwardCall from '@/components/ActiveCall/OutwardCall'

  export default {
    data() {
      return {
        radioPlayerStatus: false,
        isDark: false,
        callTime: '0 мин. 0 сек.'
      }
    },
    methods: {
      calcCallTime() {
        let h = 0, m = 0, s = 0;

        window.callTime = setInterval(() =>{
          
        s += 1

        Math.floor(s / 60) === 1 ? (m++, s = 0) : null
        Math.floor(m / 60) === 1 ? (h++, m = 0) : null
      
        this.callTime = `${h === 0 ? '' : h + ' час. '}${m + ' мин. '}${s + ' cек.'}`
        }, 1000)
      }
    },
    computed: {
      ...mapState(['call','calls','music', 'systemStyle']),
      getInterlocutorName() {
        let interlocutorName = this.calls.contacts.find(item => item.number == this.call.interlocutor)
        return interlocutorName ? interlocutorName.name : this.call.interlocutor
      },
      showCallType() {
        if (this.call.type === 'outward' || this.call.type === 'incoming' && this.call.picked)
          return OutwardCall

        else return IncomingCall
      },
      showCallInfo() {
        let isPicked = this.call.picked, type = this.call.type;

        if (!isPicked && type == 'incoming')
          return 'Вызывает'

        else if (!isPicked && type == 'outward')
          return 'Вызов...'

        else return this.callTime

      }
    },
    watch: {
      'call.picked': {
        handler(value) {
          value ? this.calcCallTime() : clearTimeout(window.callTime)
        }
      }
    },
    created() {
      this.isDark = this.systemStyle.isDark
      this.radioPlayedStatus = this.music.player.paused

      this.$store.commit('SET_PICK_STATUS', false)
      this.$store.commit('REFRESH_SYSTEM_STYLE', false)
      this.$store.dispatch('RADIO_CONTROL', true)
    },
    destroyed() {
      clearTimeout(window.callTimeout)
      this.$store.commit('REFRESH_SYSTEM_STYLE', this.isDark)
      this.$store.dispatch('RADIO_CONTROL', this.radioPlayedStatus)
    }
  }

</script>

<style lang="sass"> @import '@/assets/sass/phone/apps/active-call/main' </style>