<template lang="pug">

  keep-alive
    .home(
      :style='{background: `url(${require(`@/assets/img/wallpapers/${wallpapers[wallpaperIndex]}`)})`}'
      ).full-screen
      .apps
        .apps-list
          .apps__item(v-for='(item, index) in home.apps')
            router-link(:to='item.path')
              img(:src="require('@/assets/img/apps/svg/' + item.img)").apps__icon
              p(v-if='notifications(item.name)').apps__count {{notifications(item.name)}}
              p.apps__name {{item.name}}

        .fast-apps
          .apps__item(v-for='(item, index) in home.fastApps')
            router-link(:to='item.path')
              img(:src="require('@/assets/img/apps/svg/' + item.img)").apps__icon
              p(v-if='notifications(item.name)').apps__count {{notifications(item.name)}}

</template>

<script>

  import {mapState, mapGetters} from 'vuex'

  import moment from 'moment'

  export default {
    name: 'Apps',
    computed: {
      ...mapState({
        home: state => state.home,
        wallpapers: state => state.settings.wallpapersList,
        wallpaperIndex: state => state.settings.selectedWallpaper
      }),
      ...mapGetters({
        messagesCount: 'GET_UNREAD_MESSAGES_COUNT',
        recentCount: 'GET_ALL_UNCHECK_CALLS',
        historyBankCount: 'GET_UNCHECK_HISTORY'
      })
    },
    methods: {
      notifications(appName) {
        switch (appName) {
          case 'Сообщения': 
            return this.messagesCount === 0 ? null : this.messagesCount
            break
          case 'Недавние':
            return this.recentCount === 0 ? null : this.recentCount
            break
          case 'Платежи':
            return this.historyBankCount === 0 ? null : this.historyBankCount
            break
        }

        return null

      }
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', false)
      this.notifications('Сообщения')
    }
  }

</script>


<style lang="sass"> @import '../assets/sass/phone/apps/home/home' </style>