import Vue from 'vue'

export default {
  state: {
    categories: ['Избранное', 'Важное',  'Ближайшее', 'Работы', 'Гос.структуры', 'Банды и мафии'],
    points: [
      {name: 'Мэрия', category: 'Важное', favourite: true},
      {name: 'Работа грузчиком', category: 'Работы'},
      {name: 'Работа на заводе', category: 'Работы'},
      {name: 'Стройка', category: 'Работы'},
      {name: 'Офис', category: 'Работы', favourite: true},
      {name: 'Кафе', category: 'Ближайшее', favourite: true},
      {name: 'Магазин 24/7', category: 'Ближайшее'},
      {name: 'ФСБ', category: 'Гос.структуры'},
      {name: 'Полиция', category: 'Гос.структуры'},
      {name: 'Больница', category: 'Важное'},
      {name: 'Баллас', category: 'Банды и мафии'},
      {name: 'Грув стрит', category: 'Банды и мафии'},
    ],
    distanceToPoint: 2.3 // Дистанция до точки в километрах 
  },
  actions: {},
  mutations: {
    SELECT_POINT(state, index) {
      let isSelected = {...state.points[index]};

      state.points.map(item => Vue.delete(item, 'selected'))

      if (!isSelected.selected)
        Vue.set(state.points[index], 'selected', true)

      
    },
    SET_FAVOURITE_POINT(state, name) {
      let findPoint = state.points.find(item => item.name === name)

      findPoint.favourite === true 
        ? Vue.delete(findPoint, 'favourite')
        : Vue.set(findPoint, 'favourite', true)

    }
  },
  getters: {}
}