import gsap from 'gsap'
import '../../src/assets/sass/alerts.sass'

class BottomAlert {
  show(message) {
    let alert = document.querySelector('.alerts');
    
    alert.innerText = ''
  
    if (!window.alertTimeout) {
      gsap.fromTo(alert, 0.3, 
        {opacity: 0, display: 'none'}, 
        {opacity: 1, display: 'block'});
    }
    
    clearTimeout(window.alertTimeout);
  
    window.alertTimeout = setTimeout(() => {
      gsap.to(alert, 0.2, 
        {opacity: 0, display: 'none', onComplete: () => {
        window.alertTimeout = undefined
        alert.innerText = ''
      }});
      
      
    },2500);
   
    alert.innerText = message
  }
}


export default new BottomAlert()