
export default {
  state: {
    notesList: [
      {name: 'Заголовок', text: "текст заметки", time: 1608642946364}
    ]
  },
  mutations: {
    SAVE_NOTE(state, data) {
      let findNote = state.notesList.find(item => item.time == data.checkTime);

      delete data.checkTime

      findNote 
        ? Object.assign(findNote, data)
        : state.notesList.push(data)

    },
    DELETE_NOTE(state, time) {
      let findNote = state.notesList.findIndex(item => item.time == time)

      state.notesList.splice(findNote, 1)
    }
  },
  getters: {
    GET_NOTES_LIST_LENGTH(state) {
      return state.notesList.length
    }
  }
}