<template lang="pug">
  
  #app
    Sprites
    PhoneControl

    .phone
      .face
        .screen

          PhoneHide

          Statusbar          
          Notifications
          
          router-view
          
          transition(name='call')
            ActiveCall(v-if='activeCall')
          
          .alerts
            
          router-link.button-home(
            v-if='$route.name != "HomePage" && !activeCall'
            :to='{name: "HomePage"}'
            :class="{'button-home_dark': isDark}"
          )

</template>

<script>

  Array.prototype.last = function() {
    return this[this.length - 1];
  }

  import {mapState} from 'vuex'
  
  import Sprites from '@/components/Sprites'
  import Statusbar from '@/components/Home/Statusbar'
  import Notifications from '@/components/Home/Notifications'
  import PhoneControl from '@/components/PhoneControl'
  import PhoneHide from '@/components/PhoneHide'
  import ActiveCall from '@/views/ActiveCall'

  export default {
    name: 'Home',
    components: { Sprites, Statusbar, PhoneControl, Notifications, PhoneHide, ActiveCall},
    computed: mapState({
      isDark: state => state.systemStyle.isDark,
      player: state => state.music.player,
      radio: state => state.music.radio,
      activeCall: state => state.call.isActive,
      noDistrub: state => state.settings.noDistrub,
    }),
    watch: {
      'player.volume': {
        handler() {
          this.radio.volume = this.player.volume / 100
        }
      },
    },
  }

</script>

<style lang="sass"> @import 'assets/sass/phone/body' </style>