import Vue from 'vue'
import Vuex from 'vuex'

// modules
import home from './modules/home'
import calls from './modules/calls'
import messages from './modules/messages'
import gps from './modules/gps'
import music from './modules/music'
import settings from './modules/settings'
import notes from './modules/notes'
import bank from './modules/bank'

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    player: {
      name: 'Andrew Clover',
      phoneNumber: 45848585
    },
    systemStyle: {
      isDark: false
    },
    call: {
      isActive: false,
      picked: false,
      muted: false,
      interlocutor: 845844,
      type: 'outward', // типы: [входящий - incoming, исходящий - outward]
      ringtone: new Audio('https://zvukogram.com/mp3/869/standartnyiy-rington-uiphone-6-plus-9458.mp3')
      // ringtone: new Audio('')
    },
    phoneHidden: true
  },
  mutations: {
    REFRESH_SYSTEM_STYLE(state, bool) {
      Vue.set(state.systemStyle, 'isDark', bool);
    },
    SET_PHONE_REFLECTION(state, bool) {
      state.phoneHidden = bool
    },
    RINGTONE_CONTROL(state, bool) {
      state.call.ringtone.loop = true
      state.call.ringtone.currentTime = 0
      bool
        ? state.call.ringtone.play()
        : state.call.ringtone.pause() 
    },
    SET_CALL_STATUS(state, bool) {
      state.call.isActive = bool
    },
    SET_PICK_STATUS(state, bool) {
      state.call.picked = bool
    },
    SET_CALL_TYPE(state, type) {
      state.call.type = type
    },
    MUTE_CALL(state, bool) {
      state.call.muted = bool
    }
  },
  actions: {
    MAKE_CALL({state, getters, rootState}, data) {
      if (rootState.settings.noDistrub || state.call.isActive) 
        return

      else if (rootState.calls.blocked.includes(parseInt(data.interlocutor)))
        return

      else if (state.player.phoneNumber === parseInt(data.interlocutor))
        return

      Object.assign(state.call, data)
      state.call.isActive = true
      state.call.muted = false

    }
  },
  getters: {
    GET_GLOBAL_TIME() {
      return new Date().toLocaleTimeString('ru').replace(/...$/, '')
    }
  },
  modules: { home, calls, messages, gps, music, settings, notes, bank }
})

export default store