<template lang="pug">

  .status-bar__wrapper
    .forelock
        .forelock__speaker
        .forelock__camera
    .status-bar(:class="{'status-bar_dark': systemStyle.isDark}")
      p.status-bar__time {{time}}
      
      .states
        svg.states__icon.states__icon_service
          use(xlink:href='#service')
        svg.states__icon.states__icon_battery
          use(xlink:href='#battery')

</template>

<script>

  import {mapState, mapGetters} from 'vuex'

  export default {
    name: 'Statusbar',
    data() {
      return {
        time: '0:00'
      }
    },
    computed: {
      ...mapState(['systemStyle']),
      ...mapGetters({
        getGlobalTime: 'GET_GLOBAL_TIME'
      })
    },
    created() {
      setInterval(() => 
        this.time = this.getGlobalTime, 500
      )
    }
  }
  
</script>
