import Vue from 'vue'

export default {
  state: {
    recent: [ // Типы звонков: Пропущенный (missed), Входящий (incoming), Исходящий (outward)
      {number: 845844, type: 'missed', time: 1608541429516, checked: false},
      {number: 845844, type: 'incoming', time: 1608541435827, checked: false},
      {number: 334344, type: 'outward', time: 1608541455060, checked: false},
    ],
    contacts: [
      {name: 'Alexey WWW', number: 463465, favourite:true },
      {name: 'Настя Эскорт', number: 345345 },
      {name: 'Egor Polar', number: 845844 },
    ],
    blocked: [],
    searchContacts: ''
  },
  actions: {
    SEND_DATA() {

    }
  },
  mutations: {
    ADD_USER_TO_CONTACTS(state, contact) {
      state.contacts.push({
        name: `${contact.name} ${contact.surname}`,
        number: contact.phone
      })
    },
    ADD_CALL_TO_RECENT(state, data) {
      data.checked = false
      state.recent.push(data)
    },
    CHANGE_USER_IN_CONTACTS(state, data) {
      let findContact = state.contacts.find(item => item.number == data.prev.number);

      Vue.set(findContact, 'name', `${data.edited.name} ${data.edited.surname}`)
      Vue.set(findContact, 'number', data.edited.phone)
    },
    CHANGE_USER_FAVOURITE(state, data) {
      let findUser = state.contacts.find(item => item.number == data.number);
      
      data.action 
        ? Vue.set(findUser, 'favourite', true)
        : Vue.delete(findUser, 'favourite')
    },
    CHECK_ALL_RECENTS(state) {
      state.recent.map(item => Vue.delete(item, 'checked'))
    },
    DELETE_RECENT_FROM_CALLS(state, recent) {
      let recentIndex = state.recent.findIndex(item => item.time == recent);

      state.recent.splice(recentIndex, 1);
    },
    DELETE_CONTACT(state,number) {
      let findContact = state.contacts.findIndex(item => item.number == number);

      state.contacts.splice(findContact, 1)
    },
    BLOCK_NUMBER(state, phone) {
      let findPhone = state.blocked.indexOf(phone);

      state.blocked.includes(phone) 
        ? state.blocked.splice(findPhone, 1)
        : state.blocked.push(phone)
    }
  },
  getters: {
    GET_ALL_UNCHECK_CALLS(state) {
      let acc = 0;
      state.recent.map(({checked}) => checked === false ? acc += 1 : null)
      return acc
    }
  }
}