<template lang="pug">
  
  .calls.full-screen
    router-view
    .nav-tabs
      .nav-tabs__link(v-for='(item, index) in navIcons',)
        router-link(
          :to='{name: item.path}' 
          active-class='nav-tabs__to_selected').nav-tabs__to
          svg.nav-tabs__icon
            use(:xlink:href="'#' + item.icon")
          p.nav-tabs__name {{item.name}}
        

</template>

<script>

  import {mapState} from 'vuex'

  export default {
    name: 'Calls',
    data() {
      return {
        navIcons: [
          {name: 'Избранные', icon: 'favourite', path: 'FavouriteContacts'},
          {name: 'Недавние', icon: 'recent', path: 'RecentCalls'},
          {name: 'Контакты', icon: 'contacts', path: 'ContactsList'},
          {name: 'Клавиши', icon: 'buttons', path: 'EnterPhone'}
        ],
      }
    },
    computed: {
      ...mapState({
        contacts: state => state.calls.contacts
      })
    },
    created() {
      this.$store.commit('REFRESH_SYSTEM_STYLE', true)
    }
  }

</script>

<style lang="sass">

  @import '../assets/sass/phone/apps/calls/calls'

</style>