import gsap from 'gsap'
import router from '../router'
import store from '../store'

import '../assets/sass/phone/notification.sass'

let apps = {
  "message": {
    icon: 'messages.svg',
    name: 'Сообщения'
  },
  "bank": {
    icon: 'bank.svg',
    name: 'Платежи'
  }
}

let sound = new Audio('https://audiokaif.ru/wp-content/uploads/2019/06/Звук-уведомления-на-iphone.mp3')

let vuex = store.state

// new Notification( {app: "message", text: "текст"} )

export default class Notification {
  constructor(options) {
    this.appData = apps[options.app];
    this.appName = options.app
    this.text = options.text
    this.from = options.from
    this.contactName = vuex.calls.contacts.find(({number}) => number == this.from)
    
  }
  template() {
    return `
    <div class="notification">

      <div class="notification__header">
        <div class="notification__from">
          <img src="${require(`@/assets/img/apps/svg/${this.appData.icon}`)}" class="notification__app-icon">
          <p class="notification__app">${this.appData.name}</p>
        </div>
        <div class="notification__close">
          <svg> <use xlink:href="#cancel"> </svg>
        </div>
        <p class="notification__time">Сейчас</p>
      </div>
      <p class="notification__name">
        ${this.notificationTitle()}
      </p>
      <p class="notification__text">${this.text}</p>
    </div>
    ` 
  }
  notificationTitle() {
    switch (this.appName) {
      case 'message':
        return this.contactName ? this.contactName.name : this.from
      case 'bank': 
        return this.from
    }
  }
  playSound() {

    clearTimeout(window.radioMuteTimeout)
    window.radioMuteTimeout = null

    let radioVolume = vuex.music.player.volume

    if (!window.radioVolume) 
      window.radioVolume = radioVolume
    
    vuex.music.player.volume = 2
    
    sound.currentTime = 0
    sound.play()

    window.radioMuteTimeout = setTimeout(() => {
      vuex.music.player.volume = window.radioVolume
      window.radioVolume = null
    }, 2000)
    
  }
  show() {

    if (vuex.settings.noDistrub || vuex.call.isActive) 
      return

    window.tl 
      ? window.tl.kill()
      : null

    window.tl = gsap.timeline()

    let wrapper   = document.querySelector('.notification-wrapper');
    
    

    wrapper.onclick = () => {
        
        switch(this.appName) {
          case "message":
            router.push({name: 'MessageUser', params: {number: this.from}}).catch(() => {})
            break
          case "bank":
            router.push({name: 'Bank'}).catch(() => {})
            break
        }

        wrapper.innerHTML = ''
        window.tl.kill()
    }

    wrapper.innerHTML = ''
    wrapper.innerHTML = this.template()

    let closeBtn  = document.querySelector('.notification__close');

    closeBtn.onclick = (e) => {
      e.stopPropagation()
      wrapper.innerHTML = ''
    }

    window.tl
      .fromTo(wrapper, 0.28, {top: -200, display: 'none'}, {top: 0, display: 'block', ease: 'power1.out'})
      .to(wrapper, 0.28, {top: -200, display: 'none', ease: 'power1.in', delay: 3})

    this.playSound()

  }
}
