<template lang="pug">
  
  .notification-wrapper

</template>

<script>

  import {mapState} from 'vuex'

  export default {
    computed: {
      ...mapState({
        phoneNumber: state => state.player.phoneNumber,
        messages:    state => JSON.parse(JSON.stringify(state.messages)),
        history:    state => JSON.parse(JSON.stringify(state.bank.history)),
      })
    },
    watch: {
      messages: {
        handler(val, oldVal) {
          
          [val, oldVal].map(item => item.map(({list}) => list.sort((a,b) => b.time - a.time)))

          if (this.$route.name == 'MessageUser') return

          if (val.length > oldVal.length) {
            let lastMsg = val.last()

            if (lastMsg.list[0].from !== this.phoneNumber)
              return new this.$noty(
                {app: "message", text: lastMsg.list[0].text, from: lastMsg.list[0].from}
              ).show() 
              
          }

          else {
            val.some(({list}, index) => {
              if (list.length > oldVal[index].list.length)

                if (list[0].from !== this.phoneNumber) 
                  return new this.$noty(
                    {app: "message", text: list[0].text, from: list[0].from}
                  ).show()

            })
          }
        },
        deep: true
      },
      history: {
        handler(array, oldArray) {
          let sortedArray = array.sort((a,b) => b.time - a.time),
              firstObj    = sortedArray[0];

          if (array.length === oldArray.length)
            return

          if (firstObj.type === 'deposit' && firstObj.title === 'Пополнение счета')
              return new this.$noty(
                {app: "bank", text: `Внесено $${firstObj.money}`, from: 'Пополнение счета'}
              ).show()

        },
        deep: true
      }
    }
  }
</script>
