<template lang="pug">

  .messages-list
    p(v-if='!messages.length').phone__info Нет сообщений
    .message-item(v-else v-for='message in messageList')
      router-link(:to="{name: 'MessageUser', params: {number: message.number}}").message-item__link
        .message-user-image(:class="{'message-user-image_unread': message.unread}")
          img(
            v-if='!message.inContacts' 
            src='../../assets/img/icons/user.svg').message-user-image__img
          .message-user-image__ico(v-else)
            p.message-user-image__first-char {{message.name[0]}}
        
        .message-user-info
          .message-user-name
            h1.message-user-name__user {{message.name || message.number}}
            .message-user-name__time
              p {{calcTime(message.list.last().time)}}

          p.message-user-info__text
            span(v-if='message.list.last().from == myNumber').message-user-info__from Вы: 
            span {{message.list.last().text}}
        

</template>

<script>

  import {mapState} from 'vuex'
  import moment from 'moment'

  export default {
    props: {
      messages: Array
    },
    computed: {
      ...mapState({
        contacts: state => state.calls.contacts,
        myNumber: state => state.player.phoneNumber
      }),
      messageList() {
        let messages          = this.messages,
            contacts          = this.contacts,
            filteredMessages  = [];
            
        messages.forEach(item => {
          let findContact = contacts.find(contact => contact.number == item.number),
              message     = {...item};

          if (findContact) {
            message.name = findContact.name
            message.inContacts = true  
          }
          message.list.some((msg) => 
            msg.read === false && msg.from != this.myNumber 
            ? message.unread = true : null)

          filteredMessages.push(message);
        })

        filteredMessages.sort((a,b) => {
          return  b.list.last().time - a.list.last().time
        })

        return filteredMessages
      }
    },
    methods: {
      calcTime(time) {
        let diffHours = moment(new Date().getTime()).diff(time, 'hours'),
            diffDays  = moment(new Date().getTime()).diff(time, 'days'),
            m         = moment(time);

        let dates = {
          "В понедельник": "Пн",
          "Во вторник": "Вт",
          "В среду": "Ср",
          "В четверг": "Чт",
          "В пятницу": "Пт",
          "В субботу": "Сб",
          "В воскресенье": "Вс",
        }

        if (diffDays <= 5 && diffHours > 24) 
          return `${dates[m.calendar().split(', ')[0]]}, ${m.format('LT')}`
          
        else
          return `${m.calendar().replace('в ', '')}`

      }
    }
  }
</script>
